import React, { useState } from 'react';
import './ItemContainer.css';
import SortContainer from '../SortContainer/SortContainer';
import CropDetail from '../CropDetail/CropDetail';
import GoodDetail from '../GoodDetail/GoodDetail';
import ItemModal from '../ItemModal/ItemModal';

const ItemContainer = props =>{
  const [modalItemIdx, setModalItemIdx] = useState(null);

  const showMoreInfo = (itemIdx) => {
    setModalItemIdx(itemIdx);
  }
  
  const closeModal = e =>{
    setModalItemIdx(null);
  }

  return(
    <div className="ItemContainer">
      {modalItemIdx !== null &&
        <ItemModal 
          closeModal={closeModal}
          item={props.items[modalItemIdx]}
          itemIdx={modalItemIdx}
          itemRef={props.itemRef} 
          deleteItem={props.deleteItem}
          farmingLevel={props.farmingLevel}
          hasTiller={props.hasTiller}
          hasAgriculturist={props.hasAgriculturist} />
      }
      <SortContainer 
        sortByField={props.sortByField}
        sortByFieldName={props.sortByFieldName}
        reverseSort={props.reverseSort} />
      {props.items.length > 0 &&
        <div>
          {props.items.map((item, idx)=> {
            //only render detail containers if calculations have been made
            if(item.avgGoldPerDay){
              switch(item.displayCategory){
                case 'crop':
                  return <CropDetail 
                          key={item.id + '_' + idx}
                          item={item}
                          itemIdx={idx}
                          itemRef={props.itemRef} 
                          deleteItem={props.deleteItem}
                          farmingLevel={props.farmingLevel}
                          hasTiller={props.hasTiller}
                          hasAgriculturist={props.hasAgriculturist}
                          showMoreInfo={showMoreInfo}/>
                case 'wineJuice':
                case 'preserves':
                case 'good':
                case 'cheese':
                case 'mayo':
                case 'oil':
                  return <GoodDetail 
                          key={item.id + '_' + idx}
                          item={item} 
                          itemIdx={idx}
                          itemRef={props.itemRef}
                          deleteItem={props.deleteItem}
                          farmingLevel={props.farmingLevel}
                          hasRancher={props.hasRancher}
                          hasArtisan={props.hasArtisan} 
                          showMoreInfo={showMoreInfo}/>
                default:
                  return '';
              }
            } else return '';
          })}
          <button className="my-2 App-delete-all" onClick={props.deleteAll}>
            <img className="App-delete-all-icon" src="./icons/general/trash-can.svg" alt="trash" />
            Delete All Items
          </button>
        </div>
      }
    </div>
  )
}

export default ItemContainer;