import React from 'react';
import './PerksContainer.css';

const PerksContainer = props => {
  return(
    <div className="PerksContainer">
      <p className="">Perks:</p>
      <span>
        {props.hasTiller &&
          <span className="PerksContainer-single-perk">
            <img className="PerksContainer-perks-icon me-1" src="./icons/general/36px-Tiller.webp" alt="tiller" />
            {/* <span>Tiller</span> */}
          </span>
        }
        {props.hasRancher &&
          <span className="PerksContainer-single-perk">
            <img className="PerksContainer-perks-icon me-1" src="./icons/general/36px-Rancher.png" alt="rancher" />
            {/* <span>Rancher</span> */}
          </span>
        }
        {props.hasAgriculturist &&
          <span className="PerksContainer-single-perk">
            <img className="PerksContainer-perks-icon me-1" src="./icons/general/36px-Agriculturist.webp" alt="agriculturist" />
            {/* <span>Agriculturist</span> */}
          </span>
        }
        {props.hasArtisan &&
          <span className="PerksContainer-single-perk">
            <img className="PerksContainer-perks-icon me-1" src="./icons/general/36px-Artisan.webp" alt="artisan" />
            {/* <span>Artisan</span> */}
          </span>
        }
        {!props.hasArtisan && !props.hasAgriculturist && !props.hasTiller && !props.hasRancher &&
          <span>N/A</span>
        }
      </span>
    </div>
  )
}

export default PerksContainer;