import React from 'react';
import './InfoPanel.css';

const InfoPanel = props => {
  return(
    <div className="InfoPanel">
      <div className="InfoPanel-clickguard" onClick={props.closeInfoPanel}></div>
      <div className="InfoPanel-main">
        <p className="InfoPanel-close-btn" onClick={props.closeInfoPanel}>&times;</p>
        <h2 className="InfoPanel-heading">Stardew Valley Helper</h2>
        <div className="row">
          <p>I created this app to help with calculating data relating to crops and craftable goods in Stardew Valley. I referenced Exnil's formulas
            on their <a href="https://github.com/exnil/crop_planner" rel="noreferrer" target="_blank">GitHub respository</a> and they appear to be very accurate.
          </p>
          <p>This GitHub project is public for anyone that would like to fork or use any of my code: <a href="https://github.com/HaulinOats/sv-helper" target="_blank" rel="noreferrer">https://github.com/HaulinOats/sv-helper</a></p>
        </div>
      </div>
    </div>  
  )
}

export default InfoPanel;