import React from 'react';
import './App.css';
import helpers from '../helpers';
import soilModifiers from '../soil-modifiers';
import AddItem from './AddItem/AddItem';
import InfoPanel from './InfoPanel/InfoPanel';
import ItemContainer from './ItemContainer/ItemContainer';
import PerksContainer from './PerksContainer/PerksContainer';

//itemRef is a key/value store for every single item in the dataset. The key is the item's id.
//itemData is the dataset sorted into categories
const { itemRef, itemData } = helpers.createItemObjects();

class App extends React.Component {
  constructor(props) {
    super(props);
    
    this.state = {
      selectedItemsArr:[],
      addItemPanelIsShowing:false,
      showInfoPanel:false,
      sortByField:'addedAt',
      farmingLevel:0,
      hasTiller:false,
      hasAgriculturist:false,
      hasRancher:false,
      hasArtisan:false
    };
  }

  componentDidMount(){
    //check if dataVersion variables match, if not, purge stored items
    //change this value when needing to purge items from storage
    //should only need to change when major changes to dataset occur
    let dataVersion = 2;
    let purgeData = false;
    if(localStorage.getItem('dataVersion')){
      if(JSON.parse(localStorage.getItem('dataVersion')) !== dataVersion){
        purgeData = true;
        localStorage.setItem('dataVersion', dataVersion);
      }
    } else {
      localStorage.setItem('dataVersion', dataVersion);
      purgeData = true;
    }

    //get items from storage or set to default state values
    this.setState({
      farmingLevel:this.getItemFromStorage('farmingLevel') || this.state.farmingLevel,
      hasTiller:this.getItemFromStorage('hasTiller') || this.state.hasTiller,
      hasRancher:this.getItemFromStorage('hasRancher') || this.state.hasRancher,
      hasAgriculturist:this.getItemFromStorage('hasAgriculturist') || this.state.hasAgriculturist,
      hasArtisan:this.getItemFromStorage('hasArtisan') || this.state.hasArtisan,
      sortByField:this.getItemFromStorage('sortByField') || this.state.sortByField,
      selectedItemsArr:!purgeData ? this.getItemFromStorage('selectedItems') : false || this.state.selectedItemsArr
    }, this.updateCalculationsAndSort);

    //set dynamic background
    let currentDate = new Date();
    if(currentDate.getHours() > 7 && currentDate.getHours() < 19){
      document.body.style.backgroundImage = 'url(./day-bg.png)';
    } else {
      document.body.style.backgroundImage = 'url(./night-bg.png)';
    }
    
    //adjusts bottom container starting position based on height of header
    let height = document.querySelector('.App-top-container').offsetHeight;
    document.querySelector('.App').style.marginTop = height + 'px';
  }

  getItemFromStorage(key){
    if(localStorage.getItem(key)){
      try {
        return JSON.parse(localStorage.getItem(key));
      } catch(e) {
        return localStorage.getItem(key);
      }
    } else {
      return false;
    }
  }

  updateCalculationsAndSort(){
    let selectedItemsArr = [];
    this.state.selectedItemsArr.forEach(item =>{
      let calculationObj = {};
      switch(item.displayCategory){
        case "crop":
          calculationObj = this.getCropCalculations(item);
          break;
        case "wineJuice":
          calculationObj = this.getWineJuiceCalculations(item);
          break;
        case "preserves":
          calculationObj = this.getPreserveCalculations(item)
          break;
        case "good":
          calculationObj = this.getGoodCalculations(item);
          break;
        case "cheese":
          calculationObj = this.getCheeseCalculations(item);
          break;
        case "mayo":
          calculationObj = this.getMayoCalculations(item);
          break;
        case "oil":
          calculationObj = this.getOilCalculations(item);
          break;
        default:
      }
      selectedItemsArr.push({
        ...item,
        ...calculationObj
      });
    })

    //sort items based on sortByField value
    selectedItemsArr.sort((a,b)=>{
      if(a[this.state.sortByField] < b[this.state.sortByField]) return -1;
      if(a[this.state.sortByField] > b[this.state.sortByField]) return 1;
      return 0;
    })
    // console.log('selected items: ', selectedItemsArr);
    this.setState({selectedItemsArr}, this.updateStorage);
  }

  updateStorage(){
    localStorage.setItem('selectedItems', JSON.stringify(this.state.selectedItemsArr));
    localStorage.setItem('sortByField', this.state.sortByField);
    localStorage.setItem('farmingLevel', this.state.farmingLevel);
    localStorage.setItem('hasTiller', this.state.hasTiller);
    localStorage.setItem('hasRancher', this.state.hasRancher);
    localStorage.setItem('hasAgriculturist', this.state.hasAgriculturist);
    localStorage.setItem('hasArtisan', this.state.hasArtisan);
  }

  hideAddItemPanel(){
    this.setState({addItemPanelIsShowing:false});
  }

  showAddItemPanel(){
    this.setState({addItemPanelIsShowing:true});
  }

  setDynamicField(prop, val){
    this.setState({[prop]:val}, this.updateCalculationsAndSort);
  }

  deleteItem(idx){
    let itemsArr = this.state.selectedItemsArr;
    itemsArr.splice(idx, 1);
    this.setState({
      selectedItemsArr:itemsArr
    }, this.updateStorage)
  }

  sortByFieldName(fieldName){
    let selectedItemsArr = this.state.selectedItemsArr;
    selectedItemsArr.sort((a,b)=>{
      if(a[fieldName] < b[fieldName]) return -1;
      if(a[fieldName] > b[fieldName]) return 1;
      return 0;
    })
    this.setState({
      selectedItemsArr,
      sortByField:fieldName
    }, this.updateStorage);
  }

  reverseSort(){
    let selectedItemsArr = this.state.selectedItemsArr;
    selectedItemsArr.reverse();
    this.setState({selectedItemsArr}, this.updateStorage);
  }

  openInfoPanel(){
    this.setState({showInfoPanel:true});
  }

  closeInfoPanel(){
    this.setState({showInfoPanel:false});
  }

  deleteAll(){
    this.setState({
      selectedItemsArr:[]
    }, this.updateStorage);
  }

  addItem(item){
    let selectedItemsArr = this.state.selectedItemsArr;
    selectedItemsArr.push(item);
    this.setState({
      selectedItemsArr,
      addItemPanelIsShowing:false
    }, this.updateCalculationsAndSort);
  }

  getCropCalculations(item){
    let name = itemRef[item.itemId].name;
    let maxHarvestDays = 28;
    let harvestDays = 0;
    let maxHarvests = 0;
    let maxHarvestsRounded = 0;
    let finalHarvestDay = 1;
    let mFarmingLevel = this.state.farmingLevel;
    let sellPricePerHarvest = 1;
    let perkSellPriceMultiplier = 1;
    let initialGrowTimeMultiplier = 1;
    let regrowthGrowTimeMultiplier = 1;
    let farmingLevelAvgPriceMultiplier = 1;
    let avgGoldPerDay = 0;
    let baseSellPricePerHarvest = itemRef[item.itemId].quality.normal.sellsFor * itemRef[item.itemId].harvestYield;
    let normalSellPrice = itemRef[item.itemId].quality.normal.sellsFor;

    //check if perks are active and adjust modifiers
    if(this.state.hasTiller) perkSellPriceMultiplier += 0.10;
    if(this.state.hasAgriculturist) {
      initialGrowTimeMultiplier -= 0.10;
      regrowthGrowTimeMultiplier -= 0.10;
    }

    //apply food buff
    mFarmingLevel += item.foodFarmingBuff;
    if(mFarmingLevel > 14) mFarmingLevel = 14;

    //Get/set fertilizer modifiers
    switch(item.fertilizerId){
      case "basic_fertilizer":
        //Basic Fertilizer
        farmingLevelAvgPriceMultiplier = soilModifiers.basic[mFarmingLevel].average;
        break;
      case "quality_fertilizer":
        //Quality Fertilizer
        farmingLevelAvgPriceMultiplier = soilModifiers.quality[mFarmingLevel].average;
        break;
      case "speed_gro":
        //Speed Gro
        initialGrowTimeMultiplier -= 0.10;
        break;
      case "deluxe_speed_gro":
        //Deluxe Speed Gro
        initialGrowTimeMultiplier -= 0.25;
        break;
      case "deluxe_fertilizer":
        //Deluxe Fertilizer
        farmingLevelAvgPriceMultiplier = soilModifiers.deluxe[mFarmingLevel].average;
        break;
      case "hyper_speed_gro":
        //Hyper Speed Gro
        initialGrowTimeMultiplier -= 0.33;
        break;
      default:
        //Normal (no fertilizer)
        farmingLevelAvgPriceMultiplier = soilModifiers.normal[mFarmingLevel].average;
    }

    //set avg price multiplier to '1' if user has it explicitly turned it off
    // if(!item.pirceMultiplierIsActive) farmingLevelAvgPriceMultiplier = 1;

    //get max possible harvest days
    switch(itemRef[item.itemId].seasons.length){
      case 2:
        if(item.seasonStartIdx === 0) maxHarvestDays = 56;
        break;
      case 3:
        if(item.seasonStartIdx === 0) maxHarvestDays = 84;
        else if(item.seasonStartIdx === 1) maxHarvestDays = 56;
        break;
      default:
    }

    //if can't be grown in any particular season (Cactus Fruit)
    if(!itemRef[item.itemId].seasons.length){
      maxHarvestDays = 112;
    }

    //get actual harvest days (account for planting day)
    harvestDays = maxHarvestDays - item.plantingDay;
    
    //if using greenhouse, set max harvest days to all days in year (112)
    if(item.isGreenhouse) {
      maxHarvestDays = 112;
      harvestDays = maxHarvestDays;
    }

    //calculate max harvests possible
    if(itemRef[item.itemId].harvestTime.regrowth){
      // + 1 accounts for removal of initial growth time
      // maxHarvests = ((harvestDays - Math.ceil(itemRef[item.itemId].harvestTime.initial * growTimeMultiplier))/Math.ceil(itemRef[item.itemId].harvestTime.regrowth * growTimeMultiplier)) + 1;
      maxHarvests = ((harvestDays - Math.floor(itemRef[item.itemId].harvestTime.initial * initialGrowTimeMultiplier))/itemRef[item.itemId].harvestTime.regrowth * regrowthGrowTimeMultiplier) + 1;
      maxHarvestsRounded = Math.floor(maxHarvests);
      finalHarvestDay = Math.floor(itemRef[item.itemId].harvestTime.initial * initialGrowTimeMultiplier) + ((itemRef[item.itemId].harvestTime.regrowth * regrowthGrowTimeMultiplier) * (maxHarvestsRounded - 1)) + 1;
    } else {
      maxHarvests = harvestDays/Math.floor(itemRef[item.itemId].harvestTime.initial * initialGrowTimeMultiplier);
      maxHarvestsRounded = Math.floor(maxHarvests);
      finalHarvestDay = Math.floor(itemRef[item.itemId].harvestTime.initial * initialGrowTimeMultiplier) * maxHarvestsRounded + 1;
    }

    if(maxHarvests < 1) {
      maxHarvests = 0;
      maxHarvestsRounded = 0;
    }

    if(item.isCustom){
      normalSellPrice = item.customSellPrice;
      baseSellPricePerHarvest = item.customSellPrice * itemRef[item.itemId].harvestYield;
      sellPricePerHarvest = (itemRef[item.itemId].harvestYield * (item.customSellPrice * farmingLevelAvgPriceMultiplier)) * perkSellPriceMultiplier;
    } else {
      sellPricePerHarvest = (itemRef[item.itemId].harvestYield * (itemRef[item.itemId].quality.normal.sellsFor * farmingLevelAvgPriceMultiplier)) * perkSellPriceMultiplier;
    }

    //different calculations for if crop regrows or not
    if(itemRef[item.itemId].harvestTime.regrowth){
      //https://github.com/exnil/crop_planner
      //Formula = ((Max Harvests * Base Sell Price) - (Seed Cost + Fertilizer Cost)) / (Final Harvest Day - 1)
      avgGoldPerDay = ((maxHarvestsRounded * sellPricePerHarvest) - (item.seedCost + item.fertilizerCost))/(finalHarvestDay - 1);
    } else {
      //https://github.com/exnil/crop_planner
      //Formula = ((Max Harvests * Base Sell Price) - (Seed Cost * Max Harvests) - Fertilizer Cost) / (Final Harvest Day - 1)
      avgGoldPerDay = ((maxHarvestsRounded * sellPricePerHarvest) - ((item.seedCost * maxHarvestsRounded) - item.fertilizerCost))/(finalHarvestDay - 1);
    }

    return {
      name,
      maxHarvestDays,
      harvestDays,
      maxHarvests,
      maxHarvestsRounded,
      finalHarvestDay,
      perkSellPriceMultiplier,
      initialGrowTimeMultiplier,
      regrowthGrowTimeMultiplier,
      normalSellPrice,
      baseSellPricePerHarvest,
      sellPricePerHarvest,
      sellPricePerHarvestRounded:+(sellPricePerHarvest).toFixed(2),
      farmingLevelAvgPriceMultiplier,
      avgGoldPerDay,
      maturityDaysModified:itemRef[item.itemId].harvestTime.initial * initialGrowTimeMultiplier,
      maturityDaysModifiedRounded:Math.floor(itemRef[item.itemId].harvestTime.initial * initialGrowTimeMultiplier),
      regrowthDaysModified:itemRef[item.itemId].harvestTime.regrowth * regrowthGrowTimeMultiplier,
      regrowthDaysModifiedRounded:Math.floor(itemRef[item.itemId].harvestTime.regrowth * regrowthGrowTimeMultiplier)
    }
  }

  getWineJuiceCalculations(item){
    let isWine = item.itemId === 'wine' ? true : false;
    let basePrice = 0;
    let sellPrice = 0;
    let priceMultiplier = 1;
    let avgGoldPerDay = 0;
    let processingTime = itemRef[item.itemId].processingTime;
    let processingTimeInDays;

    if(isWine){
      basePrice = itemRef[item.ingredientId].quality.normal.sellsFor * 3;
    } else {
      basePrice = itemRef[item.ingredientId].quality.normal.sellsFor * 2.25;
    }

    //if using cask
    if(item.quality){
      processingTime = itemRef[item.itemId].quality[item.quality].processingTime;
      basePrice *= itemRef[item.itemId].quality[item.quality].caskPriceMultiplier;
    }

    if(this.state.hasArtisan) priceMultiplier += 0.4;
    sellPrice = basePrice * priceMultiplier;
    processingTimeInDays = processingTime/1600;

    //if custom 
    if(item.isCustom){
      basePrice = item.customSellPrice;
      sellPrice = basePrice;
    }
    
    avgGoldPerDay = sellPrice/processingTimeInDays;

    return {
      sellPrice,
      basePrice,
      priceMultiplier,
      processingTime,
      processingTimeInDays,
      processingTimeInDaysModified:processingTimeInDays,
      avgGoldPerDay
    }
  }

  getPreserveCalculations(item){
    let basePrice = itemRef[item.ingredientId].quality.normal.sellsFor * 2 + 50;
    let sellPrice = basePrice;
    let priceMultiplier = 1;
    let avgGoldPerDay = 0;
    let processingTime = itemRef[item.itemId].processingTime;
    let processingTimeInDays = processingTime/1600;

    if(this.state.hasArtisan) priceMultiplier += 0.4;
    sellPrice *= priceMultiplier;

    if(item.isCustom){
      basePrice = item.customSellPrice;
      sellPrice = basePrice;
    }
    
    avgGoldPerDay = sellPrice/processingTimeInDays;

    return {
      sellPrice,
      basePrice,
      priceMultiplier,
      processingTime,
      processingTimeInDays,
      processingTimeInDaysModified:processingTimeInDays,
      avgGoldPerDay
    }
  }

  getGoodCalculations(item){
    let isHoney = item.itemId === 'honey' ? true : false;
    let basePrice = itemRef[item.itemId].quality ? itemRef[item.itemId].quality.normal.sellsFor : itemRef[item.itemId].sellsFor;
    let sellPrice = basePrice;
    let getsArtisanBuff = itemRef[item.itemId].getsArtisanBuff;
    let getsRancherBuff = itemRef[item.itemId].getsRancherBuff;
    let priceMultiplier = 1;
    let avgGoldPerDay = 0;
    let processingTime = itemRef[item.itemId].processingTime;
    let processingTimeInDays;
    let processingTimeInDaysModified;

    if(isHoney){
      if(item.ingredientId) basePrice = itemRef[item.ingredientId].honeySellPrice;
      else basePrice = itemRef[item.itemId].sellsFor;
      sellPrice = basePrice;
    }

    //if using cask
    if(item.quality){
      processingTime = itemRef[item.itemId].quality[item.quality].processingTime;
      sellPrice = itemRef[item.itemId].quality[item.quality].sellsFor;
    }

    if(this.state.hasArtisan && getsArtisanBuff) priceMultiplier += 0.4;
    if(this.state.hasRancher && getsRancherBuff) priceMultiplier += 0.2;
    sellPrice *= priceMultiplier;

    processingTimeInDays = processingTime/1600;
    processingTimeInDaysModified = processingTimeInDays;

    //reduce to 2 max sales per day to be more realistic in number of crafts per day (like coffee and green tea)
    if(processingTimeInDays < 1) processingTimeInDaysModified = 1;

    //if custom 
    if(item.isCustom){
      basePrice = item.customSellPrice;
      sellPrice = basePrice;
    }
    
    avgGoldPerDay = sellPrice/processingTimeInDaysModified;

    return {
      basePrice,
      sellPrice,
      priceMultiplier,
      processingTime,
      processingTimeInDays,
      processingTimeInDaysModified:+(processingTimeInDaysModified).toFixed(2),
      avgGoldPerDay
    }
  }

  getCheeseCalculations(item){
    let basePrice = itemRef[item.itemId].quality[item.startQuality].sellsFor;
    let sellPrice = basePrice;
    let priceMultiplier = 1;
    let avgGoldPerDay = 0;
    let processingTime= itemRef[item.itemId].processingTime;
    let processingTimeInDays;
    let processingTimeInDaysModified;
    let startQuality = item.startQuality;
    let endQuality = item.quality;
    
    //if using a cask
    if(endQuality){
      sellPrice = itemRef[item.itemId].quality[endQuality].sellsFor;
      if(startQuality === 'normal') {
        processingTime = itemRef[item.itemId].quality[endQuality].processingTime;
      } else {
        processingTime = itemRef[item.itemId].quality[endQuality].processingTime - itemRef[item.itemId].quality[startQuality].processingTime;
      }
    }

    processingTimeInDays = processingTime/1600;
    processingTimeInDaysModified = processingTimeInDays;
    if(processingTimeInDays < 1) processingTimeInDaysModified = 1;

    if(this.state.hasArtisan) priceMultiplier += 0.4;
    if(this.state.hasRancher) priceMultiplier += 0.2;
    priceMultiplier = +(priceMultiplier).toFixed(2);
    sellPrice *= priceMultiplier;

    //if custom 
    if(item.isCustom){
      basePrice = item.customSellPrice;
      sellPrice = basePrice;
    }
    
    avgGoldPerDay = sellPrice/processingTimeInDaysModified;

    return {
      basePrice,
      sellPrice,
      startQuality,
      quality:endQuality ? endQuality : startQuality,
      priceMultiplier,
      processingTime,
      processingTimeInDays,
      processingTimeInDaysModified,
      avgGoldPerDay:avgGoldPerDay
    }
  }

  getMayoCalculations(item){
    let itemId = 'mayonnaise';
    let basePrice = itemRef['mayonnaise'].quality.normal.sellsFor;
    let name;
    let mayoQuantity = 1;
    let quality;
    let sellPrice = 0;
    let priceMultiplier = 1;
    let avgGoldPerDay = 0;
    let processingTime = itemRef['mayonnaise'].processingTime;
    let processingTimeInDays = processingTime/1600;
    let processingTimeInDaysModified = 1;

    switch(item.ingredientId){
      case "large_egg":
        basePrice = itemRef['mayonnaise'].quality.gold.sellsFor;
        break;
      case "ostrich_egg":
        basePrice = itemRef['mayonnaise'].quality[item.eggQuality].sellsFor;
        mayoQuantity = 10;
        quality = item.eggQuality;
        break;
      case "golden_egg":
        basePrice = itemRef[item.ingredientId].quality.gold.sellsFor;
        mayoQuantity = 3;
        break;
      case "duck_egg":
        itemId = "duck_mayonnaise";
        break;
      case "void_egg":
        itemId = "void_mayonnaise";
        break;
      case "dinosaur_egg":
        itemId = "dinosaur_mayonnaise";
        break;
      default:
    }

    if(itemId === 'mayonnaise') name = `${itemRef[itemId].name} (${itemRef[item.ingredientId].name})`;
    else name = itemRef[itemId].name;

    if(this.state.hasRancher) priceMultiplier += 0.2;
    if(this.state.hasArtisan) priceMultiplier += 0.4;

    sellPrice = (basePrice * priceMultiplier) * mayoQuantity;

    //if custom 
    if(item.isCustom){
      basePrice = item.customSellPrice;
      sellPrice = basePrice * mayoQuantity;
    }

    avgGoldPerDay = sellPrice/processingTimeInDaysModified;

    return {
      itemId,
      name,
      quality,
      quantity:mayoQuantity,
      basePrice,
      sellPrice,
      priceMultiplier,
      processingTime,
      processingTimeInDays,
      processingTimeInDaysModified,
      avgGoldPerDay
    }
  }

  getOilCalculations(item){
    let basePrice = itemRef[item.itemId].sellsFor;
    let sellPrice = basePrice;
    let getsArtisanBuff = itemRef[item.itemId].getsArtisanBuff;
    let priceMultiplier = 1;
    let avgGoldPerDay = 0;
    let processingTime = 0;
    let processingTimeInDays = 0;
    let processingTimeInDaysModified = 1;

    if(item.itemId === 'oil') processingTime = itemRef[item.ingredientId].oilProcessingTime;
    else processingTime = itemRef[item.itemId].processingTime;
    processingTimeInDays = processingTime/1600;

    if(this.state.hasArtisan && getsArtisanBuff) priceMultiplier += 0.4;
    priceMultiplier = +(priceMultiplier).toFixed(2);
    sellPrice *= priceMultiplier;

    //if custom 
    if(item.isCustom){
      basePrice = item.customSellPrice;
      sellPrice = basePrice;
    }

    avgGoldPerDay = sellPrice/processingTimeInDaysModified;

    return {
      basePrice,
      sellPrice,
      priceMultiplier,
      avgGoldPerDay,
      processingTime,
      processingTimeInDays,
      processingTimeInDaysModified
    }
  }

  render(){
    return (
      <div className="App container-fluid">
        <AddItem 
          itemRef={itemRef}
          itemData={itemData}
          soilModifiers={soilModifiers}
          addItemPanelIsShowing={this.state.addItemPanelIsShowing}
          hideAddItemPanel={this.hideAddItemPanel.bind(this)}
          addItem={this.addItem.bind(this)}
          setField={this.setDynamicField.bind(this)}
          farmingLevel={this.state.farmingLevel}
          hasTiller={this.state.hasTiller}
          hasAgriculturist={this.state.hasAgriculturist}
          hasRancher={this.state.hasRancher}
          hasArtisan={this.state.hasArtisan} />
        {this.state.showInfoPanel &&
          <InfoPanel
            openInfoPanel={this.openInfoPanel.bind(this)}
            closeInfoPanel={this.closeInfoPanel.bind(this)}/>
        }
        <div className="App-top-container" style={{ backgroundImage: `url('./wood.png')` }}>
          <div className="App-top-container-first">
            <h1 onClick={this.openInfoPanel.bind(this)}>
              <i className="App-title-first">Stardew</i>
              <i className="App-title-second">Valley</i>
              <i className="App-title-third">Helper</i>
            </h1>
            <span className="App-perks-level-container" onClick={this.showAddItemPanel.bind(this)}>
              <div className="App-perks-level-farming">
                <span>Farming Lvl:</span>
                <span>{this.state.farmingLevel}</span>
              </div>
              <PerksContainer 
                hasTiller={this.state.hasTiller}
                hasRancher={this.state.hasRancher} 
                hasAgriculturist={this.state.hasAgriculturist}
                hasArtisan={this.state.hasArtisan} />
            </span>
            <button className="App-add-item-btn" onClick={this.showAddItemPanel.bind(this)}>Add Item</button>
          </div>
        </div>
        {this.state.selectedItemsArr.length > 0 &&
          <ItemContainer 
            items={this.state.selectedItemsArr}
            itemRef={itemRef}
            itemData={itemData}
            deleteItem={this.deleteItem.bind(this)}
            deleteAll={this.deleteAll.bind(this)}
            sortByField={this.state.sortByField}
            sortByFieldName={this.sortByFieldName.bind(this)}
            reverseSort={this.reverseSort.bind(this)} 
            farmingLevel={this.state.farmingLevel}
            hasTiller={this.state.hasTiller}
            hasAgriculturist={this.state.hasAgriculturist}
            hasRancher={this.state.hasRancher}
            hasArtisan={this.state.hasArtisan} />
        }
        {/* If no items have been added */}
        {!this.state.selectedItemsArr.length &&
          <div className="App-no-items" style={{ backgroundImage: `url('./wood.png')` }}>
            <h2>Welcome to The Stardew Valley Helper!</h2>
            <p>This app was designed to help calculate daily earnings for various crops and craftable items in Stardew Valley.</p>
            <p>By default, the app will auto calculate everything for you, however, you can toggle on "Enter Values Myself" if you
               would like to enter your own prices. This feature is for those that prefer self discovery and would like to figure
               out prices on their own (with just a little help).</p>
            <p>Add some items and sort by specific criteria, or click on specific items to view more details.</p>
          </div>
        }
      </div>
    );
  }
}

export default App;
