import React from 'react';
import './GoodDetail.css';

const GoodDetail = props => {
  return(
    <div className="GoodDetail" key={props.itemId} style={{ backgroundImage: `url('./wood.png')` }} onClick={e => props.showMoreInfo(props.itemIdx)}>
      <div className="GoodDetail-top-name-level-container">
        <span className="GoodDetail-name-icon-container">
          <span className="GoodDetail-icon-container">
            {props.item.quality &&
              <span className={`GoodDetail-name-quality-star App-${props.item.quality}-color`}>&#9733;</span>
            }
            <img className="GoodDetail-main-icon" alt="crop-icon" src={'./icons/goods/' + props.itemRef[props.item.itemId].icon}/>
          </span>
          <span className="GoodDetail-main-title">{props.item.name ? props.item.name : props.itemRef[props.item.itemId].name}</span>
        </span>
        <div>
          {props.item.isCask && 
            <span className="d-inline-block">
              <img className="GoodDetail-status-icon" src="./icons/cask.svg" alt="cask" />
            </span>
          }
          {props.item.isCustom &&
            <span className="d-inline-block">
              <img className="GoodDetail-status-icon" src="./icons/custom.svg" alt="custom" />
            </span>
          }
        </div>
      </div>
      <div className="GoodDetail-main-container">
        <div className="GoodDetail-main-container-left">
          <span className="GoodDetail-bottom-container-row">
            <span>Made From:</span>
            <span>
              {(props.itemRef[props.item.ingredientId].category === 'crops' || props.itemRef[props.item.ingredientId].category === 'trees'
                || props.itemRef[props.item.ingredientId].category === 'seeds') &&
                <img className="ItemModal-icon" src={'./icons/crops/' + props.itemRef[props.item.ingredientId].icon} alt="made-from"/>
              }
              {(props.itemRef[props.item.ingredientId].category === 'goods' || props.itemRef[props.item.ingredientId].category === 'animal_products') &&
                <img className="ItemModal-icon" src={'./icons/goods/' + props.itemRef[props.item.ingredientId].icon} alt="made-from"/>
              }
              {!props.item.ingredientId &&
                <span>N/A</span>
              }
            </span>
          </span>
          <span className="GoodDetail-bottom-container-row">
            <span>Process Minutes:</span>
            <span>{props.item.processingTime}</span>
          </span>
          <span className="GoodDetail-bottom-container-row">
            <span>Process Days:</span>
            <span><i className="sv-tilde sv-black">~</i>{props.item.processingTimeInDaysModified}</span>
          </span>
        </div>
        <div className="GoodDetail-main-container-right">
          <span className="GoodDetail-bottom-container-row">
            <span>Base Sell Price:</span>
            <span><img className="GoodDetail-daily-gold-icon" src="./icons/general/27px-Gold.png" alt="gold"/>{+(props.item.basePrice).toFixed(2)}g</span>
          </span>
          <span className="GoodDetail-bottom-container-row no-border">
            <span>Actual Sell Price:</span>
            <span><img className="GoodDetail-daily-gold-icon" src="./icons/general/27px-Gold.png" alt="gold"/>{+(props.item.sellPrice).toFixed(2)}g</span>
          </span>
          <span className="GoodDetail-gold-per-day">
            <span>Gold/Day:</span>
            <span className="GoodDetail-gold-per-day-total"><img className="GoodDetail-daily-gold-icon" src="./icons/general/27px-Gold.png" alt="gold"/>{props.item.avgGoldPerDay ? +(props.item.avgGoldPerDay).toFixed(2) + 'g' : '0'}</span>
          </span>
        </div>
      </div>
    </div>
  )
}

export default GoodDetail;