import React from 'react';
import './SortContainer.css';

const SortContainer = props => {
  return(
    <div className="SortContainer">
      <div>
        <span className="SortContainer-sort-container" style={{ backgroundImage: `url('./wood.png')` }}>
          <label>Sort:</label>
          <select className="SortContainer-sort-by-dropdown" value={props.sortByField} onChange={e => props.sortByFieldName(e.target.value)}>
            <option value="addedAt">Time Added</option>
            <option value="name">Name</option>
            <option value="avgGoldPerDay">Avg. Gold Per Day</option>
            <option value="sellPrice">Sell Price (Goods)</option>
            <option value="sellPricePerHarvestRounded">Avg Sell Price (Crops)</option>
            <option value="seedCost">Seed Cost (Crops)</option>
            <option value="fertilizerCost">Fertilizer Cost (Crops)</option>
            <option value="maxHarvestsRounded">Total Harvests (Crops)</option>
          </select>
          <img className="SortContainer-reverse-sort-img" src="./icons/reverse-sort.svg" alt="reverse-sort" onClick={props.reverseSort} />
        </span>
      </div>
    </div>
  )
}

export default SortContainer;