import React from 'react';
import './ItemModal.css';
import helpers from "../../helpers";

const ItemModal = props =>{
  let itemRefObj = props.itemRef[props.item.itemId];
  return(
    <div className="ItemModal">
      <div className="ItemModal-clickguard" onClick={props.closeModal}></div>
      <div className="ItemModal-main" style={{ backgroundImage: `url('./wood.png')` }}>
        <p className="ItemModal-close" onClick={props.closeModal}>&times;</p>
        <button className="ItemModal-delete-item mb-3" onClick={e=>{ props.deleteItem(props.itemIdx); props.closeModal(e)}}>Delete Item</button>
        <div className="ItemModal-title-container">
          <a href={props.itemRef[props.item.itemId].url} rel="noreferrer" target="_blank">
            {props.item.displayCategory === 'crop' &&
              <span className="ItemModal-main-icon-container">
                <img src={'./icons/crops/' + props.itemRef[props.item.itemId].icon} alt={props.itemRef[props.item.itemId].icon} />
                <h2 className="d-inline align-bottom ms-3">{props.itemRef[props.item.itemId].name}</h2>
              </span>
            }
            {(props.item.displayCategory === 'wineJuice' || props.item.displayCategory === 'preserves' || props.item.displayCategory === 'good'
              || props.item.displayCategory === 'cheese' || props.item.displayCategory === 'mayo' || props.item.displayCategory === 'oil') &&
              <span>
                <span className="ItemModal-main-icon-container">
                  {props.item.quality &&
                    <span className={`ItemModal-name-quality-star App-${props.item.quality}-color`}>&#9733;</span>
                  }
                  <img src={'./icons/goods/' + props.itemRef[props.item.itemId].icon} alt={props.itemRef[props.item.itemId].icon} />
                </span>
                <h2 className="d-inline align-bottom ms-3">{props.item.name ? props.item.name : props.itemRef[props.item.itemId].name}</h2>
              </span>
            }
          </a>
          <div className="d-flex">
            {props.item.isGreenhouse && 
              <span>
                <img className="ItemModal-status-icon ItemModal-status-greenhouse-icon" src="./icons/greenhouse.svg" alt="greenhouse" />
              </span>
            } 
            {props.item.isCask && 
              <span>
                <img className="ItemModal-status-icon" src="./icons/cask.svg" alt="cask" />
              </span>
            } 
            {props.item.isCustom &&
              <span>
                <img className="ItemModal-status-icon" src="./icons/custom.svg" alt="custom" />
              </span>
            }
          </div>
        </div>
        <table className="ItemModal-table table table-sm table-striped table-bordered">
          <thead>
            <tr>
              <td className="ItemModal-table-col-1 d-none"></td>
              <td className="ItemModal-table-col-2 d-none"></td>
            </tr>
          </thead>
          {props.item.displayCategory === 'crop' &&
            <tbody>
              <tr>
                <td><span>Farming Level:</span></td>
                <td><span>{props.farmingLevel}</span></td>
              </tr>
              <tr>
                <td><span>Farming Level Buff:</span></td>
                <td>
                  {props.item.foodId ? 
                    <a href={props.itemRef[props.item.foodId].url} rel="noreferrer" target="_blank">
                      <img className="ItemModal-item-icon" src={'./icons/misc/' + props.itemRef[props.item.foodId].icon} alt="food"/> 
                      <span>{props.itemRef[props.item.foodId].name} (+{props.item.foodFarmingBuff})</span>
                    </a> : 'N/A'}
                </td>
              </tr>
              <tr>
                <td><span>Farming Level Modified (Buffs):</span></td>
                <td><span>{props.farmingLevel + props.item.foodFarmingBuff}</span></td>
              </tr>
              <tr>
                <td>
                  <span>
                    <a href="https://stardewvalleywiki.com/Farming" target="_blank" rel="noreferrer">Farming Professions/Perks:</a>
                  </span>
                </td>
                <td>
                  {props.hasTiller && 
                    <div className="mb-1">
                      <img className="ItemModal-perks-icon" src="./icons/general/36px-Tiller.webp" alt="tiller"/>
                      <span className="ItemModal-perks-text">Tiller <i className="d-none d-lg-inline-block">(+10% Crop Sell Price)</i></span>
                    </div>
                  }
                  {props.hasAgriculturist && 
                    <div>
                      <img className="ItemModal-perks-icon" src="./icons/general/36px-Agriculturist.webp" alt="agriculturist"/>
                      <span className="ItemModal-perks-text">Agriculturist <i className="d-none d-lg-inline-block">(10% Faster Crop Growth)</i></span>
                    </div>
                  }
                  <span>{!props.hasTiller && !props.hasAgriculturist && 'N/A'}</span>
                </td>
              </tr>
              <tr>
                <td><span>Fertilizer Used:</span></td>
                <td>
                  {props.item.fertilizerId ?
                    <a href={props.itemRef[props.item.fertilizerId].url} rel="noreferrer" target="_blank">
                      <img className="ItemModal-fertilizer-icon ItemModal-item-icon" src={'./icons/general/' + props.itemRef[props.item.fertilizerId].icon} alt="fertilizer" />
                      <span>{props.itemRef[props.item.fertilizerId].name}</span>
                    </a> : 'N/A'
                  }
                </td>
              </tr>
              <tr>
                <td><span>Fertilizer Cost:</span></td>
                <td>
                  {props.item.fertilizerId ?
                    <div>
                      <img className="ItemModal-gold-icon" src="./icons/general/27px-Gold.png" alt="gold"/>
                      <span>{props.item.fertilizerCost}g</span>
                    </div>
                  : 'N/A'}
                </td>
              </tr>
              {!props.item.isCustom &&
                <tr>
                  <td><span>Seed Vendors:</span></td>
                  <td>
                    {props.itemRef[itemRefObj.seedId].vendors.map(vendor =>{
                      return(
                        <div key={vendor.id} className="ItemModal-seed-vendor">
                          <img className="ItemModal-seed-vendor-icon" src={'./icons/general/' + props.itemRef[vendor.id].icon} alt="seed-vendor"/>
                          <span><a href={props.itemRef[vendor.id].url} rel="noreferrer" target="_blank">{props.itemRef[vendor.id].shortName}</a></span>
                          <img className="ItemModal-gold-icon" src="./icons/general/27px-Gold.png" alt="gold"/>{vendor.cost}g
                        </div>
                      )
                    })}
                  </td>
                </tr>
              }
              <tr>
                <td><span>Seed Cost:</span></td>
                <td>
                  <a href={props.itemRef[itemRefObj.seedId].url} rel="noreferrer" target="_blank">
                    <img className="ItemModal-seed-icon" src={'./icons/crops/' + props.itemRef[itemRefObj.seedId].icon} alt="seed-icon"/>
                  </a> 
                  <span>{props.item.seedCost}g</span>
                </td>
              </tr>
              <tr className="ItemModal-planting-day">
                <td><span>Planting Day:</span></td>
                <td>
                  {!props.item.isGreenhouse &&
                    <span>
                      <img className="ItemModal-season-icon" src={'./icons/general/' + helpers.seasonIcons[props.itemRef[props.item.itemId].seasons[props.item.seasonStartIdx]]} alt="season-icon"/> 
                      <span>{props.itemRef[props.item.itemId].seasons[props.item.seasonStartIdx]} {props.item.plantingDay}</span>
                    </span>
                  }
                  {props.item.isGreenhouse &&
                    <span>
                      <img className="ItemModal-season-icon" src="./icons/greenhouse.svg" alt="greenhouse-icon"/> 
                      <span>Greenhouse</span>
                    </span>
                  }
                </td>
              </tr>
              <tr>
                <td><span>Max Harvest Days:</span></td>
                <td><span>{props.item.maxHarvestDays} Days</span></td>
              </tr>
              <tr>
                <td><span>Last Harvest Day:</span></td>
                <td><span>{props.item.finalHarvestDay}</span></td>
              </tr>
              <tr>
                <td><span>Maturity Days:</span></td>
                <td><span>{props.itemRef[props.item.itemId].harvestTime.initial} Days</span></td>
              </tr>
              <tr>
                <td><span>Maturity Days Modified (Fertilizer + Perks):</span></td>
                <td><span>{props.item.maturityDaysModifiedRounded} Days</span></td>
              </tr>
              <tr>
                <td><span>Regrowth Days:</span></td>
                <td>
                  <span>{props.itemRef[props.item.itemId].harvestTime.regrowth ? props.itemRef[props.item.itemId].harvestTime.regrowth + ' Days' : 'N/A'}</span>
                </td>
              </tr>
              <tr>
                <td><span>Regrowth Days Modified (Fertilizer + Perks):</span></td>
                <td>
                  <span>
                    {props.itemRef[props.item.itemId].harvestTime.regrowth ?
                      props.item.regrowthDaysModifiedRounded + ' days' : 'N/A'
                    }
                  </span>
                </td>
              </tr>
              <tr>
                <td><span>Max Harvests Possible:</span></td>
                <td><span>{props.item.maxHarvestsRounded}</span></td>
              </tr>
              {!props.item.isCustom &&
                <tr>
                  <td><span>Crop Prices:</span></td>
                  <td>
                    <div className="ItemModal-crop-price">
                      <div className="ItemModal-crop-price-icon-container">
                        <span className="ItemModal-crop-price-star invisible">&#9733;</span>
                      </div>
                      <span className="ItemModal-crop-price-text">
                        <img className="ItemModal-gold-icon" src="./icons/general/27px-Gold.png" alt="gold"/>
                        {props.itemRef[props.item.itemId].quality.normal.sellsFor}g
                      </span>
                    </div>
                    <div className="ItemModal-crop-price">
                      <div className="ItemModal-crop-price-icon-container">
                        <span className="ItemModal-crop-price-star App-silver-color">&#9733;</span>
                      </div>
                      <span className="ItemModal-crop-price-text">
                        <img className="ItemModal-gold-icon" src="./icons/general/27px-Gold.png" alt="gold"/>
                        {props.itemRef[props.item.itemId].quality.silver.sellsFor}g
                      </span>
                    </div>
                    <div className="ItemModal-crop-price">
                      <div className="ItemModal-crop-price-icon-container">
                        <span className="ItemModal-crop-price-star App-gold-color">&#9733;</span>
                      </div>
                      <span className="ItemModal-crop-price-text">
                        <img className="ItemModal-gold-icon" src="./icons/general/27px-Gold.png" alt="gold"/>
                        {props.itemRef[props.item.itemId].quality.gold.sellsFor}g
                      </span>
                    </div>
                    <div className="ItemModal-crop-price">
                      <div className="ItemModal-crop-price-icon-container">
                        <span className="ItemModal-crop-price-star App-iridium-color">&#9733;</span>
                      </div>
                      <span className="ItemModal-crop-price-text">
                        <img className="ItemModal-gold-icon" src="./icons/general/27px-Gold.png" alt="gold"/>
                        {props.itemRef[props.item.itemId].quality.iridium.sellsFor}g
                      </span>
                    </div>
                  </td>
                </tr>
              }
              <tr>
                <td>Harvest Yield:
                  <p><i>Crops Per Pull</i></p>
                </td>
                <td>{props.itemRef[props.item.itemId].harvestYield}</td>
              </tr>
              <tr>
                <td>Normal Sell Price:</td>
                <td>
                  <img className="ItemModal-gold-icon" src="./icons/general/27px-Gold.png" alt="gold"/>
                  <span>{props.item.normalSellPrice}g</span>
                </td>
              </tr>
              <tr>
                <td>
                  <p>Base Sell Price:</p>
                  <p><i>Normal Crop Sell Price x Harvest Yield</i></p>
                </td>
                <td>
                  <img className="ItemModal-gold-icon" src="./icons/general/27px-Gold.png" alt="gold"/>
                  <span>{+(props.item.baseSellPricePerHarvest).toFixed(2)}g</span>
                </td>
              </tr>
              <tr>
                <td>
                  <span>Average Selling Price Multiplier (<a href="https://stardewvalleywiki.com/Farming#Farming_Skill" target="_blank" rel="noreferrer">Crop Quality Frequency</a>):</span>
                </td>
                <td>
                  <span>{+(props.item.farmingLevelAvgPriceMultiplier).toFixed(2)}</span>
                </td>
              </tr>
              <tr>
                <td>
                  <span>Perks Price Multiplier:</span>
                </td>
                <td>
                  <span>{props.item.perkSellPriceMultiplier}</span>
                </td>
              </tr>
              <tr>
                <td>
                  <p>Base Sell Price Modified:</p>
                  <p><i>Base Sell Price x Average Selling Price Multiplier x Perk Price Multiplier</i></p>
                </td>
                <td>
                  <img className="ItemModal-gold-icon" src="./icons/general/27px-Gold.png" alt="gold"/>
                  <span>{props.item.sellPricePerHarvestRounded}g</span>
                </td>
              </tr>
              <tr className="ItemModal-gold-per-day-container">
                <td><span>Gold Per Day:</span></td>
                <td>
                  <img className="ItemModal-gold-icon" src="./icons/general/27px-Gold.png" alt="gold"/>
                  <span>{+(props.item.avgGoldPerDay).toFixed(2)}g</span>
                </td>
              </tr>
              <tr>
                <td colSpan="2">
                  <i>Gold Per Day is result of combining selling prices and perk modifiers with costs of fertilizer and seeds, if applicable</i>
                </td>
              </tr>
          </tbody>
        }
        {(props.item.displayCategory === 'wineJuice' || props.item.displayCategory === 'preserves' || props.item.displayCategory === 'good'
          || props.item.displayCategory === 'cheese' || props.item.displayCategory === 'mayo' || props.item.displayCategory === 'oil') &&
          <tbody>
            <tr>
              <td><span>Farming Level:</span></td>
              <td><span>{props.farmingLevel}</span></td>
            </tr>
            <tr>
              <td><a href="https://stardewvalleywiki.com/Farming" target="_blank" rel="noreferrer">Farming Professions/Perks:</a>
              </td>
              <td>
                {props.hasRancher && 
                  <div className="mb-1">
                    <img className="ItemModal-perks-icon" src="./icons/general/36px-Rancher.png" alt="rancher"/>
                    <span className="ItemModal-perks-text">Rancher <i className="d-none d-lg-inline-block">(+20% Animal Goods Price)</i></span>
                  </div>
                  }
                {props.hasArtisan && 
                  <div>
                    <img className="ItemModal-perks-icon" src="./icons/general/36px-Artisan.webp" alt="artisan"/>
                    <span className="ItemModal-perks-text">Artisan <i className="d-none d-lg-inline-block">(Artisan Goods Worth 40% More)</i></span>
                  </div>
                }
                {(!props.hasRancher && !props.hasArtisan) && 'N/A'}
              </td>
            </tr>
            <tr>
              <td><span>Made From:</span></td>
              <td>
                {(props.itemRef[props.item.ingredientId].category === 'crops' || props.itemRef[props.item.ingredientId].category === 'trees'
                   || props.itemRef[props.item.ingredientId].category === 'seeds') &&
                  <a className="d-table-cell" href={props.itemRef[props.item.ingredientId].url} rel="noreferrer" target="_blank">
                    <img className="ItemModal-icon" src={'./icons/crops/' + props.itemRef[props.item.ingredientId].icon} alt="made-from"/>
                    <span className="align-middle">{props.itemRef[props.item.ingredientId].name}</span>
                  </a>
                }
                {(props.itemRef[props.item.ingredientId].category === 'goods' || props.itemRef[props.item.ingredientId].category === 'animal_products') &&
                  <a className="d-table-cell" href={props.itemRef[props.item.ingredientId].url} rel="noreferrer" target="_blank">
                    <img className="ItemModal-icon" src={'./icons/goods/' + props.itemRef[props.item.ingredientId].icon} alt="made-from"/>
                    <span className="align-middle">{props.itemRef[props.item.ingredientId].name}</span>
                  </a>
                }
                {!props.item.ingredientId &&
                  <span>N/A</span>
                }
              </td>
            </tr>
            <tr>
              <td>Equipment:</td>
              <td>
                {props.itemRef[props.item.itemId].equipment.map((equipment, idx) => {
                  return(
                    <div key={equipment + idx} className="mb-2">
                      <a className="d-table-cell" href={props.itemRef[equipment].url} rel="noreferrer" target="_blank">
                        <img className="ItemModal-artisan-equipment-icon" src={'./icons/general/' + props.itemRef[equipment].icon} alt="equipment"/>
                        <span className="align-middle">{props.itemRef[equipment].name}</span>
                      </a>
                    </div>
                  )
                })}
              </td>
            </tr>
            {props.item.cropId &&
              <tr>
                <td><span>Base Crop Price:</span></td>
                <td>
                  <img className="ItemModal-gold-icon" src="./icons/general/27px-Gold.png" alt="gold"/>
                  <span>{props.itemRef[props.item.cropId].quality.normal.sellsFor}g</span>
                </td>
              </tr>
            }
            <tr>
              <td><span>Processing Time in Minutes:</span></td>
              <td><span>{props.item.processingTime} Minutes</span></td>
            </tr>
            <tr>
              <td><span>Processing Time in Days:</span></td>
              <td><span><i className="sv-tilde">~</i>{+(props.item.processingTimeInDays).toFixed(2)} Days</span></td>
            </tr>
            {(props.item.processingTimeInDays <= 1) &&
              <tr>
                <td>
                  <span>Processing Time in Days Modified:
                    <br></br>
                    <i>More realistic for number of crafts per day</i>
                  </span>
                </td>
                <td><span><i className="sv-tilde">~</i>{props.item.processingTimeInDaysModified} Days</span></td>
              </tr>
            }
            {props.item.startQuality &&
              <tr>
                <td><span>Base Quality:</span></td>
                <td><span className="text-capitalize">{props.item.startQuality}</span></td>
              </tr>
            }
            {props.item.quality &&
              <tr>
                <td><span>Final Quality:</span></td>
                <td><span className="text-capitalize">{props.item.quality}</span></td>
              </tr>
            }
            <tr>
              <td><span>Base Good Price:</span></td>
              <td>
                <img className="ItemModal-gold-icon" src="./icons/general/27px-Gold.png" alt="gold"/>
                <span>{+(props.item.basePrice).toFixed(2)}g</span>
              </td>
            </tr>
            {!props.item.isCustom &&
              <tr>
                <td><span>Perks Price Multiplier:</span></td>
                <td><span>{+(props.item.priceMultiplier).toFixed(2)}</span></td>
              </tr>
            }
            {props.item.quantity &&
              <tr>
                <td><span>Quantity Per Craft:</span></td>
                <td><span>{props.item.quantity}</span></td>
              </tr>
            }
            <tr>
              <td><span>Actual Sell Price:<br></br><i>Base Good Price x Modifiers x Quantity</i></span></td>
              <td>
                <img className="ItemModal-gold-icon" src="./icons/general/27px-Gold.png" alt="gold"/>
                <span>{+(props.item.sellPrice).toFixed(2)}g</span>
              </td>
            </tr>
            <tr className="ItemModal-gold-per-day-container">
              <td><span>Gold Per Day:</span></td>
              <td>
                <img className="ItemModal-gold-icon" src="./icons/general/27px-Gold.png" alt="gold"/>
                <span>{+(props.item.avgGoldPerDay).toFixed(2)}g</span>
              </td>
            </tr>
            {/* Warn users if certain Artisan item doesn't get Artisan buff */}
            {!props.itemRef[props.item.itemId].getsArtisanBuff &&
              <tr>
                <td colSpan="2">
                  <i>This particular item does not get the Artisan buff</i>
                </td>
              </tr>
            }

            {props.item.displayCategory === 'mayo' &&
              <tr>
                <td colSpan="2"><i>Only Mayonnaise made with Ostrich Eggs is affected by egg quality</i></td>
              </tr>
            }
          </tbody>
        }
        </table>
      </div>
    </div>
  )
}

export default ItemModal;