import React, { useState } from 'react';
import './LevelPerksSelection.css';

const LevelPerksSelection = props => {
  const [farmingLevel, setFarmingLevel] = useState(props.farmingLevel);
  
  const farmingLevelOnChange = (e) => {
    let tFarmingLevel = Math.round(Number(e.target.value));

    if(tFarmingLevel < 5){
      props.setField('hasTiller', false);
      props.setField('hasRancher', false);
    }
    if(tFarmingLevel < 10){
      props.setField('hasAgriculturist', false);
      props.setField('hasArtisan', false);
    }

    if(tFarmingLevel > 10) {
      tFarmingLevel = 10;
    } else if(tFarmingLevel < 0) {
      tFarmingLevel = 0;
    }

    setFarmingLevel(tFarmingLevel);

    if(e.type === 'blur') {
      props.setField('farmingLevel', tFarmingLevel);
    }
  }

  const toggleTiller = e => {
    props.setField('hasTiller', e.target.checked)

    if(e.target.checked) {
      props.setField('hasRancher', false)
    } else {
      props.setField('hasArtisan', false)
      props.setField('hasAgriculturist', false)
    }
  }

  const toggleRancher = e => {
    props.setField('hasRancher', e.target.checked)

    if(e.target.checked) {
      props.setField('hasTiller', false)
      props.setField('hasArtisan', false)
      props.setField('hasAgriculturist', false)
    }
  }
  
  const toggleAgriculturist = e => {
    props.setField('hasAgriculturist', e.target.checked)

    if(e.target.checked) {
      props.setField('hasArtisan', false)
    }
  }
  
  const toggleArtisan = e => {
    props.setField('hasArtisan', e.target.checked)

    if(e.target.checked) {
      props.setField('hasAgriculturist', false)
    }
  }

  return(
    <div className="LevelPerksSelection">
      <div className="form-group mt-2">
          <label className="LevelPerksSelection-farming-level-label user-select-none">Farming Level:</label>
          <input 
            className="LevelPerksSelection-farming-level-input" 
            type="number"
            min="0"
            max="10"
            value={farmingLevel}
            onClick={e => e.target.select()} 
            onChange={farmingLevelOnChange} 
            onBlur={farmingLevelOnChange}
            onKeyDown={e=>{if(e.key === 'Enter') e.target.blur()}} />
        </div>
        {farmingLevel > 4 &&
          <div>
            <div className="LevelPerksSelection-perk-container form-group mt-2">
              <div className="App-slider-container">
                <div className="App-slider-inner">
                  <label className="switch" htmlFor="tiller-checkbox">
                    <input type="checkbox" id="tiller-checkbox" checked={props.hasTiller} onChange={toggleTiller}/>
                    <div className="slider round"></div>
                  </label>
                </div>
                <img className="LevelPerksSelection-perk-icon align-middle" src="./icons/general/36px-Tiller.webp" alt="tiller" />
                <label className="LevelPerksSelection-checkbox-label form-check-label ms-2 user-select-none" htmlFor="tiller-checkbox">Tiller (Crops Worth 10% More)</label>
              </div>
            </div>
            <div className="LevelPerksSelection-perk-container form-group mt-2">
              <div className="App-slider-container">
                <div className="App-slider-inner">
                  <label className="switch" htmlFor="rancher-checkbox">
                    <input type="checkbox" id="rancher-checkbox" checked={props.hasRancher} onChange={toggleRancher}/>
                    <div className="slider round"></div>
                  </label>
                </div>
                <img className="LevelPerksSelection-perk-icon align-middle" src="./icons/general/36px-Rancher.png" alt="rancher" />
                <label className="LevelPerksSelection-checkbox-label form-check-label ms-2 user-select-none" htmlFor="rancher-checkbox">Rancher (Animal Products Worth 20% More)</label>
              </div>
            </div>
          </div>
        }
        {farmingLevel > 9 && props.hasTiller &&
          <div>
            <div className="LevelPerksSelection-perk-container form-group mt-2">
              <div className="App-slider-container">
                <div className="App-slider-inner">
                  <label className="switch" htmlFor="agriculturist-checkbox">
                    <input type="checkbox" id="agriculturist-checkbox" checked={props.hasAgriculturist} onChange={toggleAgriculturist}/>
                    <div className="slider round"></div>
                  </label>
                </div>
                <img className="LevelPerksSelection-perk-icon align-middle" src="./icons/general/36px-Agriculturist.webp" alt="agriculturist" />
                <label className="LevelPerksSelection-checkbox-label form-check-label ms-2 user-select-none" htmlFor="agriculturist-checkbox">Agriculturist (Crops Grow 10% Faster)</label>
              </div>
            </div>
            <div className="LevelPerksSelection-perk-container form-group mt-2">
              <div className="App-slider-container">
                <div className="App-slider-inner">
                  <label className="switch" htmlFor="artisan-checkbox">
                    <input type="checkbox" id="artisan-checkbox" checked={props.hasArtisan} onChange={toggleArtisan}/>
                    <div className="slider round"></div>
                  </label>
                </div>
                <img className="LevelPerksSelection-perk-icon align-middle" src="./icons/general/36px-Artisan.webp" alt="artisan" />
                <label className="LevelPerksSelection-checkbox-label form-check-label ms-2 user-select-none" htmlFor="artisan-checkbox">Artisan (Artisan Goods Worth 40% More)</label>
              </div>
            </div>
          </div>
        }
    </div>
  )
}

export default LevelPerksSelection;